import React from 'react'
import { Button } from "@/components/ui/button"
import Link from 'next/link'


export default function NotFound() {
  return (
    <div className='flex flex-col items-center justify-center h-screen'>
        <h1 className='mt-2 '>Opps something went wrong...</h1>
        <h1 className='mt-1'>Are you sure you are in the correct path?</h1>
        <Button type='button' size={'lg'} variant={'secondary'} className='mt-2 rounded-lg bg-slate-800 hover:bg-slate-600'>
            <Link className='text-slate-400' href="localhost:3000/">Return Home Page</Link>
        </Button>
    </div>
  )
}
